
.services-page .call-box {
  width: 100%;
  height: 11rem;
  background-color: #fff;
  margin-top: .5rem;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.call-box .service {
  display: block;
  width: 8rem;
  height: 8rem;
  background: url(../../assets/images/service_07.png) no-repeat;
  background-size: cover;
  margin-left: 1rem;
  margin-right: 1rem;
}
.call-box .telephone {
  font-size: 2rem;
  font-weight: 400;
  color: #3c363e;
}
.call-box .time {
  font-size: 1.4rem;
  color: #817b82;
  margin-top: .5rem;
}
.service-theme {
  width: 100%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.service-theme .themes {
  width: 96%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 1rem auto;
  box-sizing: border-box;
}
.service-theme .themes .theme {
  width: 11rem;
  height: 8rem;
  text-align: center;
  line-height: 8rem;
  border-radius: 5px;
  background-size: cover;
  background-repeat: no-repeat;
}
.service-theme .themes .theme a{
  color: #fff;
  font-size: 1.4rem;
}
.feedback {
  margin-right: 1rem;
  background: url(../../assets/images/service_01.png);
}
.offline {
  margin-right: 1rem;
  background: url(../../assets/images/service_03.png);
}
.point {
  background: url(../../assets/images/service_05.png);
}
.service-container{
  width: 100%;
  clear:both;
}
.service-content{
  clear: both;
  background-color: #fff;
}
.services .item {
  width: 50%;
  padding:1rem;
  float: left;
  text-align: center;
  box-sizing: border-box;
  background-color: #fff;
}
.service-content .item a {
  color: #3c363e;
  font-size: 1.4rem;
}



