
.phone-list {
  background-color: #fff;
}
.phone-header .title{
  font-size: 1.6rem;
  height: 4.6rem;
  line-height: 4.6rem;
  color: #666;
  font-weight: 500;
  margin-left: 2rem;
}
.phone-list .list .item{
  width: 100%;
  height: 12rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.item-left{
  width: 9.6rem;
  height: 9.6rem;
}
.item-left img{
  display: block;
  width: 8rem;
  height: 8rem;
}
.item-right {
  margin-left: 2rem;
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
}
.item-right .phonetitle {
  line-height: 2;
  font-size: 1.6rem;
  font-weight: bold;
  color: #000;
}
.item-right .desc {
  line-height: 2;
  color: #a2a0a3;
  font-size: 1.2rem;
}
.item-right .price {
  line-height: 2;
  color: #f55669;
  font-size: 1.4rem;
}



