
.mint-header{
  background-color: #fff;
  height: 4.4rem;
  color: #3c363e;
  font-size: 1.6rem;
  /*align-items: center;*/
}
.mint-header .gohome{
  display: block;
  width: 1.8rem;
  height: 1.8rem;
  background: url(../../assets/images/home.png) no-repeat;
  background-size: cover;
  /*margin-right: 1rem;*/
  position: absolute;
  top: 1rem;
  right: 1rem;
}
.phone-list .item{
  position: relative;
}
.phone-list .item:before{
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 1px;
  background: #f1f1f1;
}




