}
.dialog .foot .btn[data-v-1c0dab0a] {
  width: 50%;
  display: block;
  text-align: center;
  font-size: 1.6rem;
  line-height: 4rem;
}
.dialog .foot .btn[data-v-1c0dab0a]:first-child {
  position: relative;
}
.dialog .foot .btn[data-v-1c0dab0a]:first-child:after {
  position: absolute;
  content:'';
  top: 0;
  bottom: 0;
  right: 0;
  width: 1px;
  background-color: #cecece;
}




