
.accessories-box {
  width:100%;
}
.list{
  width: 100%;
  /*height: 10rem;*/
  background-color: #fff;
}
.top{
  position: relative;
  background:  #efefef;
}
.title{
  font-size: 1.6rem;
  height: 4.6rem;
  line-height: 4.6rem;
  color: #666;
  font-weight: 500;
  margin-left: 2rem;
}
.more {
  font-size: 1.4rem;
  position: absolute;
  right: 2rem;
  top: 2rem;
  color: #666;
}
.goods-list .list .item{
  float: left;
  width: 50%;
  height: 21rem;
  position:relative;
  background-color: #fff;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 0;
}
.goods-list .list .item:nth-child(2n+1):before {
  content:'';
  position: absolute;
  top:0;
  right:0;
  bottom:0;
  width:.1rem;
  background: #efefef;
}
.goods-list .list .item:after {
  content:'';
  position: absolute;
  left:0;
  right:0;
  bottom:0;
  height:.1rem;
  background: #efefef;
}
.goods-list .list .item .item-image{
  width: 10rem;
  height: 10rem;
  margin: 2rem auto 0 ;
  /*display: block;*/
}
.item-image img{
  width: 100%;
}
.item-info{
  width: 100%;
  text-align: center;
}
.item-info .goodtitle {
  line-height: 1.5;
  font-size: 1.4rem;
  color: #000;
  font-weight: normal;
  white-space:nowrap;
  text-overflow:ellipsis;
  overflow: hidden;
}
.item-info .desc {
  line-height: 1.5;
  font-size: 1.4rem;
  color: #999;
}
.item-info .price {
  line-height: 1.5;
  color:  #f55669;
  font-size: 1.4rem ;
}



