
.good-page .header{
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.good-page .header .gohome{
  display: block;
  width: 1.8rem;
  height: 1.8rem;
  margin-left: 1rem;
  margin-right: 1rem;
  background: url(../../assets/images/home.png) no-repeat;
  background-size: cover;
}
.good-page .header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.good-page .header .input-box {
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  width: 20rem;
  height: 3rem;
  border: 1px solid #cdced1;
  padding: .8rem;
  box-sizing: border-box;
}
.good-page .header .search-btn {
  width: 4.4rem;
  height: 4.4rem;
  line-height: 4.4rem;
  text-align: center;
  color: #817e83;
  font-size: 1.4rem;
}
.Active {
  padding: .4rem;
  border-radius: .3rem;
  color: #fff !important;
  background: #f55669;
}
.good-page .swiper-container2{
  width: 100%;
  overflow-x: hidden;
  height: 4rem;
  line-height: 4rem;
  background: #fff;
  border-top: 1px solid #efefef;
  border-bottom: 1px solid #efefef;
}
.good-page .swiper-container2 .swiper-wrapper{
  width: 100%;
}
.good-page .swiper-container2 .swiper-slide {
  float: left;
  width: 7rem !important;
  text-align: center;
}
.good-page .swiper-container2 .swiper-slide .type{
  width: 100%;
  font-size: 1.4rem;
  color: #444;
}
.type-list {
  width: 100%;
  margin: 0 auto;
  overflow-x: auto;
  background: #fff;
  border-top: 1px solid #eee;
  position: relative;
}
.type-list .types {
  width: 100%;
  padding: 1rem;
  display: table;
  table-layout: fixed;
  overflow-x: auto
}
.type-list .types .type {
  margin-left: 1rem;
  font-size: 1.4rem;
  float: left;
}




