
.footer {
  clear: both;
  width: 100%;
  height: 18rem;
}
.links{
  text-align: center;
  padding-top: 3rem;
  margin: 1rem;
}
.links .link{
  font-size: 1.4rem;
  color: #6a6a6a;
}
.footer .copyrights,.footer .copyrights a {
    color: #a2a2a2;
    font-size: 1.2rem;
    margin: 0 auto;
    line-height: 2rem;
    padding-bottom:2rem;
    text-align: center;
}



